<template>
  <div id='accountBalance'>
    <div class="black">
      <div
        v-if="$route.query.accountAmount>0"
        class="f22"
      >{{ $route.query.accountAmount | point(2) }}</div>
      <div
        v-else
        class="f22"
      >0</div>
      <div class="f12">账户余额(元)</div>
      <div
        v-if="is_cooperationCharge == true"
        class="flex"
      >
        <div
          class="button"
          @click="openShow('充值')"
        >充值</div>
        <div
          class="button"
          @click="openShow('退款')"
        >退款</div>
      </div>
    </div>
    <!-- 开始 -->
    <div>
      <van-pull-refresh
        v-model="pullLoading"
        @refresh="onPullRefresh"
        success-text="刷新成功"
      >
        <van-list
          v-model="listLoading"
          @load="onLoadList"
          :offset="30"
          :finished="finishedList"
          finished-text="没有更多了"
        >
          <div class='top-title'>账户明细</div>
          <div
            v-for="(value,key) in listObj"
            :key="key"
          >
            <div>
              <div
                class="time-style"
                @click="arrowTest(key)"
              >
                <div>{{ key | dataFilter('YYYY年MM月') }}</div>
                <van-icon :name="value.isTurn == true?'arrow':'arrow-down'" />
              </div>
              <div v-if="value.isTurn == true">
                <div
                  v-for="(item,i) in value.value"
                  :key="i"
                  class="bb"
                >
                  <div
                    v-if="cooperRecordTypeAll"
                    class="content-style"
                  >
                    <div>{{ cooperRecordTypeAll[item.billType] }}&ensp;{{ item.roomInfo | splitRoomSn }}</div>
                    <div :style="{color:(item.billType == '1')?'#ff7d41':'#000'}">{{ item.billType == '1'?'+':'-' }}¥{{ Math.abs(item.amount) | point(2) }}</div>
                  </div>
                  <div class="content-style c_98">
                    <div>{{ item.payTime }}</div>
                    <div>账户余额 {{ item.leftAmount | point(2) }}</div>
                  </div>
                  <div class="content-style c_98">
                    <div></div>
                    <div>{{ item.payeeAccount }}</div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </van-list>
      </van-pull-refresh>
    </div>
    <!-- 到账 -->
    <van-popup
      v-model="charge_refund_show"
      class="show-box"
    >
      <div class="show-box-content">
        <p class="f16">{{ title }}</p>
        <div class="pay-room none">
          <span>{{ title == '充值'?'到账日期':'退款日期' }}</span>
          <div class="border">
            <input
              @click="time_show = true"
              class="center"
              readonly
              :placeholder="title == '充值'?'请选择到账日期':'请选择退款日期'"
              type="text"
              v-model="showForm.payTime"
            >
          </div>
        </div>
        <div class="pay-room none">
          <span>{{ title == '充值'?'收款账户':'付款账户' }}</span>
          <div
            class="border display"
            @click="payeeAccount_show = true"
          >
            <input
              class="center"
              readonly
              :placeholder="title == '充值'?'请选择收款账户':'请选择付款账户'"
              type="text"
              v-model="payeeAccountLabel"
            >
            <van-icon name="arrow-down" />
          </div>
        </div>
        <div class="pay-room none">
          <span>{{ title == '充值'?'预收金额':'退款金额' }}</span>
          <div class="border display">
            <input
              class="center"
              :placeholder="title == '充值'?'请输入预收金额':'请输入退款金额'"
              type="text"
              v-model="showForm.amount"
            >
          </div>
        </div>
        <van-button
          class="show-button"
          @click="myCommit"
        >确定</van-button>
      </div>
    </van-popup>
    <!-- 选择到账日期 -->
    <van-calendar
      v-model="time_show"
      :max-date="maxDate"
      :min-date="minDate"
      @confirm="onConfirmTime"
    />
    <!-- 收款账户 -->
    <van-popup
      style="width: 100%"
      v-model="payeeAccount_show"
      position="bottom"
    >
      <van-picker
        show-toolbar
        :columns="users"
        @confirm="onConfirmUser"
        @cancel="payeeAccount_show = false"
      />
    </van-popup>
  </div>
</template>
<script>
import dayjs from 'dayjs';
import { isPermission } from '@/utils/filters';
import { GetSystemDictDataByType } from '@/api/api_query';
import { GetBusinessHotelPayeeAccountByhotel } from '@/api/api_business';
import { GetBusinessCooperationChargeRecord, PostBusinessCooperationCharge, PostBusinessCooperationRefund } from '@/api/api_agreement';
import { Toast } from 'vant';
export default {
    data() {
        return {
            dataForm: {
                pageNum: 0,
                pageSize: 20,
                cooperId: this.$route.query.cooperId
            },
            showForm: {
                cooperId: this.$route.query.cooperId,
                amount: null,
                payeeAccountId: null,
                payTime: null
            },
            listLoading: false, //  上滑列表加载（每一次上滑的时候）---加载中
            finishedList: false, //  上滑列表加载完了所有数据---没有更多了）
            pullLoading: false, // 下拉刷新加载动画---加载中
            isTurn: true,
            listObj: {}, //列表数据
            listData: [],
            title: null,
            payeeAccountLabel: null,
            cooperRecordTypeAll: [],
            users: [],
            maxDate: new Date(),
            minDate: new Date(),
            charge_refund_show: false,
            time_show: false,
            payeeAccount_show: false,
            is_cooperationCharge: true
        };
    },
    created() {
        this.$store.commit('setHeader', '账户余额');
        this.is_cooperationCharge = isPermission('business:cooperation:charge');
    },
    methods: {
        arrowTest(key) {
            this.listObj[key].isTurn = !this.listObj[key].isTurn;
        },
        //  下拉刷新列表
        onPullRefresh() {
            this.listData = [];
            this.dataForm.pageNum = 0;
            this.finishedList = false;
            this.onLoadList();
        },
        onLoadList() {
            GetBusinessCooperationChargeRecord(this.dataForm).done((res) => {
                this.getType();
                this.listData = this.listData.concat(res.data);
                this.listLoading = this.pullLoading = false;
                if (res.total == this.listData.length) {
                    this.finishedList = true;
                } else if (res.total > this.listData.length) {
                    this.finishedList = false;
                }
                if (this.listData.length > 0) {
                    let obj = {};
                    this.listData.sort((a, b) => {
                        return Date.parse(b.payTime) - Date.parse(a.payTime);
                    });
                    this.listData.map((item) => {
                        let payTime = dayjs(item.payTime).format('YYYY-MM');
                        if (obj[payTime]) {
                            obj[payTime].value.push(item);
                        } else {
                            obj[payTime] = {
                                value: [item],
                                isTurn: true
                            };
                        }
                    });
                    this.listObj = obj;
                }
            });
            this.dataForm.pageNum++;
        },
        getType() {
            GetSystemDictDataByType({ dictType: 'business_cooper_record_type' }).done((res) => {
                this.cooperRecordTypeAll = {};
                res.data.map((item) => {
                    this.cooperRecordTypeAll[item.dictValue] = item.dictLabel;
                });
                GetBusinessHotelPayeeAccountByhotel({ hotelId: this.$route.query.hotelId }).done((res) => {
                    this.userOldAll = res.data;
                    this.users = res.data.map((item) => item.accountName);
                });
            });
        },
        openShow(type) {
            this.showForm.amount = null;
            this.showForm.payeeAccountId = null;
            this.payeeAccountLabel = null;
            this.title = type;
            this.showForm.payTime = dayjs().format('YYYY-MM-DD');
            this.minDate = new Date(dayjs().subtract(3, 'month'));
            this.maxDate = new Date(dayjs().add(3, 'month'));
            this.charge_refund_show = true;
        },
        onConfirmTime(date) {
            this.showForm.payTime = dayjs(date).format('YYYY-MM-DD');
            this.time_show = false;
        },
        onConfirmUser(val) {
            this.payeeAccountLabel = val + '>';
            this.showForm.payeeAccountId = this.userOldAll.find((item) => item.accountName == val).id;
            this.payeeAccount_show = false;
        },
        myCommit() {
            if (!this.showForm.payeeAccountId) {
                Toast({
                    message: '请选择收款账户',
                    duration: 900
                });
                return;
            }
            if (!this.showForm.amount) {
                Toast({
                    message: '请输入预收金额',
                    duration: 900
                });
                return;
            }
            let _api = this.title == '充值' ? PostBusinessCooperationCharge : PostBusinessCooperationRefund;
            _api(this.showForm).done((res) => {
                this.charge_refund_show = false;
                Toast({
                    message: res.msg,
                    duration: 900
                });
                this.$route.query.accountAmount = res.data.leftAmount;
                this.listData = [];
                this.listObj = {};
                this.dataForm.pageNum = 0;
                setTimeout(this.onLoadList(), 1000);
            });
        }
    }
};
</script>
<style scoped>
#accountBalance {
    color: #000;
    font-size: 14px;
    overflow: scroll;
    left: 0;
    top: 0;
    height: 92%;
}
.black {
    background: #000;
    padding: 40px 0;
    color: #fff;
}
.button {
    background: #fff;
    color: #000;
    border-radius: 10px;
    width: 20%;
    line-height: 28px;
    font-size: 12px;
    font-weight: bold;
}
/* 弹出框 */
.show-box {
    width: 90%;
    border-radius: 10px;
    font-size: 14px;
    color: rgba(0, 0, 1);
}
.show-box-content {
    padding: 1rem;
}
.show-button {
    width: 100%;
    background: #ff7d41;
    color: #fff;
    height: 2.5rem;
    margin-top: 1rem;
}
.pay-room {
    display: flex;
    justify-content: space-between;
    padding: 0.5rem;
    border-bottom: 1px solid #e5e5e5;
}
.none {
    border: none;
    line-height: 40px;
    padding: 0.5rem 0;
    width: 100%;
}
.f16 {
    font-weight: 600;
    font-size: 16px;
    padding: 0.5rem 0 1rem;
}
.border {
    border: 1px solid #d7d7db;
    border-radius: 5px;
    width: 60%;
}
.display {
    display: flex;
    align-items: center;
    justify-content: center;
}
.center {
    text-align: center;
    width: 60%;
}
.img-top {
    vertical-align: text-top;
}
.top-title {
    text-align: left;
    padding: 10px;
    font-weight: bold;
    background: #ebebeb;
}
.time-style {
    text-align: left;
    padding: 10px;
    background: #ebebeb;
    display: flex;
    align-items: center;
}
.content-style {
    display: flex;
    justify-content: space-between;
    padding: 4px 10px;
}
.f22 {
    font-size: 22px;
    margin-bottom: 5px;
}
.f12 {
    font-size: 12px;
    margin-bottom: 15px;
}
.flex {
    display: flex;
    justify-content: space-evenly;
}
.c_98 {
    color: #989898;
    font-size: 12px;
}
.bb {
    border-bottom: 1px solid #ebebeb;
    padding: 10px 0;
}
</style>



